"use client";

import { useState, useRef, useCallback } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Progress } from "@/components/ui/progress";
import { Separator } from "@/components/ui/separator";
import { toast } from "sonner";
import Link from "next/link";

interface Outfit {
  id: string;
  name: string;
  category: string;
  price: number;
  brand: string;
  image: string;
  affiliateLinks: {
    amazon?: string;
    myntra?: string;
    ajio?: string;
    meesho?: string;
  };
}

const mockOutfits: Outfit[] = [
  {
    id: "shirt-1",
    name: "Classic White Shirt",
    category: "shirts",
    price: 1299,
    brand: "Brand A",
    image: "https://placehold.co/300x400?text=Classic+White+Formal+Shirt",
    affiliateLinks: {
      amazon: "#",
      myntra: "#"
    }
  },
  {
    id: "shirt-2",
    name: "Casual Blue Denim Shirt",
    category: "shirts",
    price: 1599,
    brand: "Brand B",
    image: "https://placehold.co/300x400?text=Casual+Blue+Denim+Shirt",
    affiliateLinks: {
      amazon: "#",
      ajio: "#"
    }
  },
  {
    id: "pant-1",
    name: "Black Formal Trousers",
    category: "pants",
    price: 1899,
    brand: "Brand C",
    image: "https://placehold.co/300x400?text=Black+Formal+Trousers",
    affiliateLinks: {
      myntra: "#",
      meesho: "#"
    }
  },
  {
    id: "kurta-1",
    name: "Traditional Cotton Kurta",
    category: "kurta",
    price: 999,
    brand: "Brand D",
    image: "https://placehold.co/300x400?text=Traditional+Cotton+Kurta",
    affiliateLinks: {
      amazon: "#",
      myntra: "#"
    }
  }
];

export default function TryOnPage() {
  const [uploadedImage, setUploadedImage] = useState<string | null>(null);
  const [selectedOutfits, setSelectedOutfits] = useState<Outfit[]>([]);
  const [isProcessing, setIsProcessing] = useState(false);
  const [processingProgress, setProcessingProgress] = useState(0);
  const [tryOnResult, setTryOnResult] = useState<string | null>(null);
  const [activeCategory, setActiveCategory] = useState("shirts");
  const fileInputRef = useRef<HTMLInputElement>(null);

  const categories = [
    { id: "shirts", name: "Shirts", count: 150 },
    { id: "pants", name: "Pants", count: 120 },
    { id: "kurta", name: "Kurta", count: 80 },
    { id: "saree", name: "Saree", count: 200 },
    { id: "shoes", name: "Shoes", count: 90 },
    { id: "accessories", name: "Accessories", count: 60 }
  ];

  const handleImageUpload = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        toast.error("Image size should be less than 5MB");
        return;
      }

      const reader = new FileReader();
      reader.onload = (e) => {
        setUploadedImage(e.target?.result as string);
        toast.success("Photo uploaded successfully!");
      };
      reader.readAsDataURL(file);
    }
  }, []);

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
  }, []);

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    const file = e.dataTransfer.files[0];
    if (file && file.type.startsWith('image/')) {
      if (file.size > 5 * 1024 * 1024) {
        toast.error("Image size should be less than 5MB");
        return;
      }

      const reader = new FileReader();
      reader.onload = (e) => {
        setUploadedImage(e.target?.result as string);
        toast.success("Photo uploaded successfully!");
      };
      reader.readAsDataURL(file);
    }
  }, []);

  const selectOutfit = (outfit: Outfit) => {
    const isAlreadySelected = selectedOutfits.some(item => item.id === outfit.id);
    
    if (isAlreadySelected) {
      setSelectedOutfits(prev => prev.filter(item => item.id !== outfit.id));
      toast.info(`${outfit.name} removed from selection`);
    } else {
      // For mix & match, allow multiple items from different categories
      const sameCategory = selectedOutfits.filter(item => item.category === outfit.category);
      if (sameCategory.length > 0) {
        // Replace item from same category
        setSelectedOutfits(prev => 
          prev.filter(item => item.category !== outfit.category).concat(outfit)
        );
        toast.info(`${outfit.name} replaced in selection`);
      } else {
        setSelectedOutfits(prev => [...prev, outfit]);
        toast.success(`${outfit.name} added to selection`);
      }
    }
  };

  const startTryOn = async () => {
    if (!uploadedImage) {
      toast.error("Please upload your photo first");
      return;
    }

    if (selectedOutfits.length === 0) {
      toast.error("Please select at least one outfit");
      return;
    }

    setIsProcessing(true);
    setProcessingProgress(0);

    // Simulate AI processing with progress
    const progressInterval = setInterval(() => {
      setProcessingProgress(prev => {
        if (prev >= 90) {
          clearInterval(progressInterval);
          return 90;
        }
        return prev + Math.random() * 15;
      });
    }, 200);

    // Simulate API call delay
    setTimeout(() => {
      clearInterval(progressInterval);
      setProcessingProgress(100);
      
      // Generate mock result image
      const outfitNames = selectedOutfits.map(o => o.name.replace(/\s+/g, '+')).join('+with+');
      setTryOnResult(`https://placehold.co/400x600?text=AI+Try-On+Result+${outfitNames}`);
      
      setIsProcessing(false);
      toast.success("Try-on complete! Check your result below.");
    }, 3000);
  };

  const saveToWishlist = () => {
    toast.success("Look saved to wishlist!");
  };

  const shareResult = (platform: string) => {
    toast.success(`Sharing to ${platform}!`);
  };

  const filteredOutfits = mockOutfits.filter(outfit => outfit.category === activeCategory);

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white">
      {/* Header */}
      <header className="border-b bg-white/80 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <Link href="/" className="flex items-center space-x-2">
                <div className="w-8 h-8 bg-gradient-to-r from-purple-600 to-pink-600 rounded-lg"></div>
                <h1 className="text-2xl font-bold bg-gradient-to-r from-purple-600 to-pink-600 bg-clip-text text-transparent">
                  AI Try-On
                </h1>
              </Link>
            </div>
            <nav className="hidden md:flex items-center space-x-6">
              <Link href="/" className="text-gray-700 hover:text-purple-600 font-medium">
                Home
              </Link>
              <Link href="/try-on" className="text-purple-600 font-medium">
                Try-On
              </Link>
              <Link href="/store" className="text-gray-700 hover:text-purple-600 font-medium">
                Store
              </Link>
              <Link href="/profile" className="text-gray-700 hover:text-purple-600 font-medium">
                Profile
              </Link>
            </nav>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Left Column - Photo Upload & Try-On Result */}
          <div className="lg:col-span-1">
            <Card className="mb-6">
              <CardHeader>
                <CardTitle>Upload Your Photo</CardTitle>
                <CardDescription>
                  Upload a full-body photo for the best try-on experience
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div
                  className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center cursor-pointer hover:border-purple-400 transition-colors"
                  onDragOver={handleDragOver}
                  onDrop={handleDrop}
                  onClick={() => fileInputRef.current?.click()}
                >
                  {uploadedImage ? (
                    <div className="space-y-4">
                      <img 
                        src={uploadedImage} 
                        alt="Uploaded" 
                        className="max-w-full h-64 object-cover mx-auto rounded-lg"
                      />
                      <p className="text-sm text-gray-600">Click to change photo</p>
                    </div>
                  ) : (
                    <div className="space-y-4">
                      <div className="w-16 h-16 bg-gray-200 rounded-full mx-auto flex items-center justify-center">
                        <div className="w-8 h-8 bg-gray-400 rounded-full"></div>
                      </div>
                      <div>
                        <p className="text-lg font-medium">Drop your photo here</p>
                        <p className="text-sm text-gray-600">or click to browse</p>
                        <p className="text-xs text-gray-500 mt-2">Max size: 5MB</p>
                      </div>
                    </div>
                  )}
                </div>
                <input
                  ref={fileInputRef}
                  type="file"
                  accept="image/*"
                  onChange={handleImageUpload}
                  className="hidden"
                />
              </CardContent>
            </Card>

            {/* Selected Outfits */}
            {selectedOutfits.length > 0 && (
              <Card className="mb-6">
                <CardHeader>
                  <CardTitle>Selected Outfits ({selectedOutfits.length})</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    {selectedOutfits.map((outfit) => (
                      <div key={outfit.id} className="flex items-center space-x-3 p-2 bg-gray-50 rounded-lg">
                        <img 
                          src={outfit.image} 
                          alt={outfit.name}
                          className="w-12 h-12 object-cover rounded"
                          onError={(e) => {
                            e.currentTarget.src = `https://placehold.co/100x100?text=${outfit.name}`;
                          }}
                        />
                        <div className="flex-1">
                          <p className="font-medium text-sm">{outfit.name}</p>
                          <p className="text-xs text-gray-600">₹{outfit.price}</p>
                        </div>
                        <Button
                          size="sm"
                          variant="outline"
                          onClick={() => selectOutfit(outfit)}
                        >
                          Remove
                        </Button>
                      </div>
                    ))}
                  </div>
                  
                  <Separator className="my-4" />
                  
                  <Button 
                    onClick={startTryOn}
                    disabled={isProcessing || !uploadedImage}
                    className="w-full bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-700 hover:to-pink-700"
                  >
                    {isProcessing ? "Processing..." : "Start AI Try-On"}
                  </Button>

                  {isProcessing && (
                    <div className="mt-4 space-y-2">
                      <div className="flex justify-between text-sm">
                        <span>Processing...</span>
                        <span>{Math.round(processingProgress)}%</span>
                      </div>
                      <Progress value={processingProgress} className="w-full" />
                    </div>
                  )}
                </CardContent>
              </Card>
            )}

            {/* Try-On Result */}
            {tryOnResult && (
              <Card>
                <CardHeader>
                  <CardTitle>Your Try-On Result</CardTitle>
                  <CardDescription>
                    AI-generated result with selected outfits
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <img 
                      src={tryOnResult} 
                      alt="Try-on result"
                      className="w-full rounded-lg"
                      onError={(e) => {
                        e.currentTarget.src = "https://placehold.co/400x600?text=Try-On+Result";
                      }}
                    />
                    
                    <div className="flex space-x-2">
                      <Button 
                        onClick={saveToWishlist}
                        variant="outline" 
                        className="flex-1"
                      >
                        Save to Wishlist
                      </Button>
                      <Button 
                        onClick={() => shareResult("Instagram")}
                        variant="outline"
                        className="flex-1"
                      >
                        Share
                      </Button>
                    </div>

                    <div className="grid grid-cols-2 gap-2">
                      <Button 
                        onClick={() => shareResult("WhatsApp")}
                        size="sm"
                        variant="outline"
                        className="text-green-600 border-green-200 hover:bg-green-50"
                      >
                        WhatsApp
                      </Button>
                      <Button 
                        onClick={() => shareResult("Instagram")}
                        size="sm"
                        variant="outline"
                        className="text-pink-600 border-pink-200 hover:bg-pink-50"
                      >
                        Instagram
                      </Button>
                    </div>
                  </div>
                </CardContent>
              </Card>
            )}
          </div>

          {/* Right Column - Outfit Selection */}
          <div className="lg:col-span-2">
            <Card>
              <CardHeader>
                <CardTitle>Choose Outfits</CardTitle>
                <CardDescription>
                  Select outfits to try on. You can mix and match from different categories.
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Tabs value={activeCategory} onValueChange={setActiveCategory}>
                  <TabsList className="grid w-full grid-cols-3 lg:grid-cols-6">
                    {categories.map((category) => (
                      <TabsTrigger 
                        key={category.id} 
                        value={category.id}
                        className="text-xs"
                      >
                        {category.name}
                      </TabsTrigger>
                    ))}
                  </TabsList>

                  {categories.map((category) => (
                    <TabsContent key={category.id} value={category.id} className="mt-6">
                      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
                        {filteredOutfits.map((outfit) => {
                          const isSelected = selectedOutfits.some(item => item.id === outfit.id);
                          
                          return (
                            <Card 
                              key={outfit.id}
                              className={`cursor-pointer transition-all duration-200 hover:shadow-md ${
                                isSelected ? 'ring-2 ring-purple-500 bg-purple-50' : ''
                              }`}
                              onClick={() => selectOutfit(outfit)}
                            >
                              <CardContent className="p-3">
                                <div className="aspect-[3/4] rounded-lg overflow-hidden mb-3">
                                  <img 
                                    src={outfit.image} 
                                    alt={outfit.name}
                                    className="w-full h-full object-cover"
                                    onError={(e) => {
                                      e.currentTarget.src = `https://placehold.co/200x300?text=${outfit.name}`;
                                    }}
                                  />
                                </div>
                                
                                <div className="space-y-2">
                                  <h4 className="font-medium text-sm line-clamp-2">{outfit.name}</h4>
                                  <p className="text-xs text-gray-600">{outfit.brand}</p>
                                  <div className="flex items-center justify-between">
                                    <span className="font-bold text-sm">₹{outfit.price}</span>
                                    {isSelected && (
                                      <Badge className="bg-purple-600 text-white text-xs">
                                        Selected
                                      </Badge>
                                    )}
                                  </div>
                                  
                                  <div className="flex space-x-1">
                                    {outfit.affiliateLinks.amazon && (
                                      <Badge variant="outline" className="text-xs">Amazon</Badge>
                                    )}
                                    {outfit.affiliateLinks.myntra && (
                                      <Badge variant="outline" className="text-xs">Myntra</Badge>
                                    )}
                                  </div>
                                </div>
                              </CardContent>
                            </Card>
                          );
                        })}
                      </div>
                    </TabsContent>
                  ))}
                </Tabs>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
}
